'use client'

import ScrollReveal from '@/components/ScrollReveal'

export default function ServicesPage() {
  const items = [
    { 
      title: 'Web Development', 
      body: 'Next.js, React, Node.js, API design',
      img: '/images/services/web.png'
    },
    { 
      title: 'UI/UX Design', 
      body: 'Design systems, prototyping, research',
      img: '/images/services/design.png'
    },
    { 
      title: 'Brand Strategy', 
      body: 'Identity, messaging, positioning',
      img: '/images/services/brand.png'
    },
  ]

  return (
    <main className="min-h-screen bg-[#0b0b0b] pt-32 pb-24">

      <ScrollReveal>
        <h1 className="text-center text-white text-5xl font-bold mb-16">
          Our Services
        </h1>
      </ScrollReveal>

      <div className="container mx-auto grid grid-cols-1 md:grid-cols-3 gap-12">
        {items.map((item, i) => (
          <ScrollReveal key={i} delay={i * 0.2}>
            <div className="
              bg-[#111111] 
              p-10 
              rounded-2xl 
              border border-white/10 
              shadow 
              hover:shadow-[0_0_25px_rgba(0,255,120,0.4)]
              hover:-translate-y-2 
              transition
            ">
              <img 
                src={item.img} 
                className="h-28 mx-auto mb-6 object-contain" 
                alt=""
              />

              <h3 className="text-white text-2xl font-semibold text-center">
                {item.title}
              </h3>

              <p className="text-gray-300 text-center mt-3 text-lg">
                {item.body}
              </p>
            </div>
          </ScrollReveal>
        ))}
      </div>
    </main>
  )
}
